import React, { Component } from 'react';
import Profile from './Profile';
/*
 The list component will take the list of items passed in as a property
 and create a list of Profile with those item.
*/
class List extends Component {
   renderList() {
       // map an item to a profile using its name as key
       const items = this.props.items.map(item => {
           return <Profile key={item.name} character={item} />
       });

       return items;
   }

   // Returns a list of profiles
   render() {
       return (
           <div id="content_profiles">
               {this.renderList()}
           </div>
       );
   }
}

export default List;


