import React, { Component } from 'react';
import { Button, DropdownButton, MenuItem, ToggleButtonGroup, ToggleButton } from 'react-bootstrap';
import List from './List';

class FilteredList extends Component {
   constructor(props) {
       super(props);

       // Initializes the state
       this.state = {
           search: "",
           species_filter: "",
           species_title: "Species",
           sex_filter: "",
           sex_title: "Sex",
           season_filter: "",
           season_title: "Season",
           sort_method: ""
       };
   }

   // Sets the state whenever the user types on the search bar
   onSearch = (event) => {
         this.setState({search: event.target.value.toLowerCase()});
   }

   filterItem = (item) => {
       // Checks if the current search term is contained in this item and if its attributes satisfy those of the filters
       return (item.name.toLowerCase().search(this.state.search) !== -1)
         && (item.species.search(this.state.species_filter) !== -1)
         && (item.sex.search(this.state.sex_filter) !== -1)
         && (item.season.search(this.state.season_filter) !== -1);
   }

   // sets the species filter to the user's option
   selectSpecies = (eventKey) => {
        this.setState({species_filter: eventKey});
        if (eventKey !== "") {
                this.setState({species_title: eventKey});
        } else {
            this.setState({species_title: "All Species"});
        }
   }

    // sets the sex filter to the user's option
    selectSex = (eventKey) => {
        this.setState({sex_filter: eventKey});
        if (eventKey !== "") {
                this.setState({sex_title: eventKey});
        } else {
            this.setState({sex_title: "All Sexes"});
        }
   }

    // sets the season filter to the user's option
    selectSeason = (eventKey) => {
        this.setState({season_filter: eventKey});
        if (eventKey !== "") {
                this.setState({season_title: "Season " + eventKey});
        } else {
            this.setState({season_title: "Any Season"});
        }
   }

   // sets the sort method to 1 (relevance) or 2 (age)
   setSortMethod = (value) => {
        this.setState({sort_method: value});
   }

   // Sorts the items list if sort method is 2 (age). Takes in a list and returns the sorted list
   sortItems = (items) => {
        if (this.state.sort_method === 2) {
            items.sort(this.ageComparator);
        }
        return items;
   }

   // Compare function for comparing the ages of the characters
   ageComparator = (a, b) => {
        return a.age - b.age;
   }

   // Reset the filters
   // This function does not clear the search bar nor the sort methed
   clearFilters = () => {
        this.setState({species_filter: ""});
        this.setState({species_title: "Species"});
        this.setState({sex_filter: ""});
        this.setState({sex_title: "Sex"});
        this.setState({season_filter: ""});
        this.setState({season_title: "Season"});
   }

   //The content of the page, including headers, buttons and the list of profiles of characters
   render() {
       return (
            <div className="filter-list">
               <h1>BOJACK HORSEMAN</h1>
               <h2>Main Characters Catalogue</h2>
               <div>
                 <DropdownButton id="speciesDd" className="dropDown" title={this.state.species_title} onSelect={this.selectSpecies}>
                    <MenuItem eventKey="">All</MenuItem>
                    <MenuItem eventKey="Human">Human</MenuItem>
                    <MenuItem eventKey="Animal">Animal</MenuItem>
                </DropdownButton>
                <DropdownButton id="sexDd" className="dropDown" title={this.state.sex_title} onSelect={this.selectSex}>
                    <MenuItem eventKey="">All</MenuItem>
                    <MenuItem eventKey="Male">Male</MenuItem>
                    <MenuItem eventKey="Female">Female</MenuItem>
                </DropdownButton>
                <DropdownButton id="seasonDd" className="dropDown" title={this.state.season_title} onSelect={this.selectSeason}>
                    <MenuItem eventKey="">Any</MenuItem>
                    <MenuItem eventKey="1">1</MenuItem>
                    <MenuItem eventKey="2">2</MenuItem>
                    <MenuItem eventKey="3">3</MenuItem>
                    <MenuItem eventKey="4">4</MenuItem>
                </DropdownButton>
                <input id="search" type="text" placeholder="Search" onChange={this.onSearch} />
                <Button id="clearFilters" onClick={this.clearFilters}>Clear Filters</Button>
                <ToggleButtonGroup id="sortButton" type="radio" name="sortOptions" defaultValue={1} onChange={this.setSortMethod}>
                    <ToggleButton value={1}>Sort by Relevance</ToggleButton>
                    <ToggleButton value={2}>Sort by Age</ToggleButton>
                </ToggleButtonGroup>
               </div>
               <List items={this.sortItems(this.props.items.filter(this.filterItem))} /> 
           </div>
       );
   }
}
export default FilteredList;


