import React, { Component } from 'react';

/*
    This creates a Profile component. A Profile component takes in a character and makes a visual profile for it.
*/
class Profile extends Component {

	render() {
		return (
			<div id="profile">
				<img src={this.props.character.img} alt={this.props.character.name} id="pfp"></img>
				<b id="name">{this.props.character.name}</b>
				<div id="info">
					<div>Age: {this.props.character.age}</div>
					<div>{this.props.character.description}</div>
				</div>
			</div>
		);
	}

}

export default Profile;



